@import './modules.scss';

body {
	background-color: #222;
	color: #eee;
	font-family: 'Roboto';
}
.alchy {
	@include flexbox;
	.main, .sidebar, .sidebarleft {
		width: 100%;
	}
	flex-direction: column;
	flex-wrap: wrap;
	@include md {
		justify-content: center;
		align-items: stretch;
		flex-wrap: nowrap;
		flex-direction: row;
		.main {
			width: 47%;
		}
		.sidebar {
			width: 33%;
		}
		.sidebarleft {
			width: 20%;
		}
	}
}

.floatingtext {
	@include noselect;
	pointer-events: none;
	position: absolute;
	opacity: 0;
	visibility: hidden;
	z-index: 100;
	@include transition(.3s opacity, .3s margin);
	&.visible {
		opacity: 0;
		visibility: visible;
		@include animation('moveUp 1s linear');
	}
}

@include keyframes(moveUp) {
	0% {
		opacity: 0;
		margin-top: -10px;
	}
	50% {
		opacity: 1;
	}
	100% {
		opacity: 0;
		margin-top: -50px;
	}
}

.btnRow {
	@include flexbox;
	justify-content: flex-start;
	align-items: center;
	margin: 8px 0;
	.btn {
		margin: 0 4px;
	}
	&.mg-top {
		margin-top: 30px;
	}
}

.btn {
	padding: 6px 12px;
	border: 1px solid #000;
	@include transition(.3s);
	cursor: pointer;
	&:hover {
		background-color: #333;
	}
}

svg {
	-webkit-filter: drop-shadow(2px 3px 2px rgba(0, 0, 0, 1));
	filter: drop-shadow(2px 3px 2px rgba(0, 0, 0, 1));
}


.slime svg {
	color: $slime;
}
.blood svg {
	color: $blood;
}

.slimetific-flask svg {
	color: $slime;
}
.healing-flask svg {
	color: $blood;
}
