

@mixin mazeSizes() {
	&.cols-5 {
		max-width: 300px;
		.cell {
			height: 56px;
			line-height: 56px;
			width: calc(20% - 4px);
		}
	}
	&.cols-10 {
		max-width: 400px;
		.cell {
			width: calc(10% - 4px);
		}
	}
	&.cols-11 {
		max-width: 440px;
		.cell {
			width: calc(9.089% - 4px);
		}
	}
	&.cols-12 {
		max-width: 480px;
		.cell {
			width: calc(8.33% - 4px);
		}
	}
	&.cols-13 {
		max-width: 520px;
		.cell {
			width: calc(7.69% - 4px);
		}
	}
	&.cols-14 {
		max-width: 560px;
		.cell {
		width: calc(7.14% - 4px);
		}
	}
	&.cols-15 {
		max-width: 450px;
		.cell {
			height: 26px;
			line-height: 26px;
			width: calc(6.66% - 4px);
		}
	}
	&.cols-16 {
		max-width: 510px;
		.cell {
			height: 26px;
			line-height: 26px;
			width: calc(6.25% - 4px);
		}
	}
	&.cols-17 {
		max-width: 540px;
		.cell {
			height: 26px;
			line-height: 26px;
			width: calc(5.88% - 4px);
		}
	}
	&.cols-18 {
		max-width: 450px;
		.cell {
			height: 21px;
			line-height: 21px;
			width: calc(5.55% - 4px);
		}
	}
	&.cols-18 {
		max-width: 450px;
		.cell {
			height: 21px;
			line-height: 21px;
			width: calc(5.55% - 4px);
		}
	}
	&.cols-19 {
		max-width: 475px;
		.cell {
			height: 21px;
			line-height: 21px;
			width: calc(5.26% - 4px);
		}
	}
	&.cols-20 {
		max-width: 500px;
		.cell {
			height: 21px;
			line-height: 21px;
			width: calc(5% - 4px);
		}
	}
	&.cols-21 {
		max-width: 525px;
		.cell {
			height: 21px;
			line-height: 21px;
			width: calc(4.76% - 4px);
		}
	}
	&.cols-22 {
		max-width: 550px;
		.cell {
			height: 21px;
			line-height: 21px;
			width: calc(4.54% - 4px);
		}
	}
	&.cols-23 {
		max-width: 575px;
		.cell {
			height: 21px;
			line-height: 21px;
			width: calc(4.34% - 4px);
		}
	}
	&.cols-24 {
		max-width: 480px;
		.cell {
			height: 16px;
			line-height: 16px;
			width: calc(4.16% - 4px);
		}
	}
	&.cols-25 {
		max-width: 500px;
		.cell {
			height: 16px;
			line-height: 16px;
			width: calc(4% - 4px);
		}
	}
	&.cols-26 {
		max-width: 520px;
		.cell {
			height: 16px;
			line-height: 16px;
			width: calc(3.84% - 4px);
		}
	}
	&.cols-27 {
		max-width: 540px;
		.cell {
			height: 16px;
			line-height: 16px;
			width: calc(3.69% - 4px);
		}
	}
	&.cols-28 {
		max-width: 560px;
		.cell {
			height: 16px;
			line-height: 16px;
			width: calc(3.57% - 4px);
		}
	}
	&.cols-29 {
		max-width: 580px;
		.cell {
			height: 16px;
			line-height: 16px;
			width: calc(3.44% - 4px);
		}
	}
	&.cols-30 {
		max-width: 600px;
		.cell {
			height: 16px;
			line-height: 16px;
			width: calc(3.33% - 4px);
		}
	}

	&.cols-10, &.cols-11,  &.cols-12,  &.cols-13,  &.cols-14 {
		.cell {
			font-size: 16px;
			line-height: 40px;
			&.boss {
				color: #822;
				font-size: 24px;
			}
			&.smallBoss {
				color: #622;
				font-size: 18px;
			}
		}
	}
	&.cols-15, &.cols-16,  &.cols-17 {
		.cell {
			font-size: 14px;
			line-height: 28px;
			&.boss {
				color: #822;
				font-size: 18px;
			}
			&.smallBoss {
				color: #622;
				font-size: 14px;
			}
		}
	}
	&.cols-18, &.cols-19,  &.cols-20 {
		.cell {
			font-size: 12px;
			line-height: 20px;
			&.boss {
				color: #822;
				font-size: 16px;
			}
			&.smallBoss {
				color: #622;
				font-size: 12px;
			}
		}
	}

	&.cols-14 {
		max-width: 560px;
		.cell {
			height: 36px;
			line-height: 36px;
			width: calc(7.1428% - 4px);
		}
		@include maxlg {
			max-width: 420px;
			.cell {
				height: 26px;
				line-height: 26px;
				width: calc(7.1428% - 4px);
			}
		}
	}
}
