@import '../modules';

.ReactorComponent {
	margin: 20px;
	@include flexbox;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	.reactor {
		height: 250px;
		margin: 10px;
		width: 250px;
		border: 1px solid #000;
		@include flexbox;
		justify-content: flex-start;
		align-items: flex-start;
		flex-direction: column;
		position: relative;
		z-index: 1;
		@include transition(.3s);
		&.undefined {
			&:after {
				content: 'click here';
				font-size: 32px;
				text-align: center;
				line-height: 250px;
				position: absolute;
				top: 0; left: 0; width: 100%; height: 100%;
				z-index: 2;
				background-color: rgba(#000, .4);
				@include transition(.3s);
			}
			&:hover:after {
				background-color: rgba(#fff, .2);
			}
		}
		&.isRunning {
			background-color: rgba($slime, .2);
		}
		&.selected {
			border: 1px solid #ff0;
			@include defBoxShadow;
		}
		.row {
			@include flexbox;
			justify-content: space-between;
			align-items: center;
			margin: 15px;
			width: 220px;
			@include transition(.3s);
			.inputContainer {
				height: 160px;
				width: 120px;
				border: 1px solid #000;
				position: relative;
				@include transition(.3s);
				&.w-100 {
					width: 220px;
				}
				&.w-80 {
					width: 170px; // 220*.8
				}
				&.w-20 {
					width: 40px;
				}
				&:after {
					position: absolute;
					content: '';
					top: 100%;
					left: 50%;
					height: 16px;
					width: 4px;
					background-color: #000;
				}
			}
		}
		.outputContainer {
			margin: 0 15px;
			height: 40px;
			width: 220px;
			border: 1px solid #000;
			position: relative;
			@include transition(.3s);
			.bar {
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				z-index: 1;
				@include transition(.3s);
			}
			.label {
				z-index: 2;
				position: relative;
				width: 100%;
				line-height: 40px;
				text-align: center;
				@include transition(.3s);
			}
		}
		.inputContainer {
			position: relative;
			.bar {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				z-index: 1;
				@include transition(1s);
			}
			.label {
				z-index: 2;
				position: relative;
				width: 100%;
				line-height: 160px;
				text-align: center;
				@include transition(.3s);
			}
		}
		&.shrink {
			height: 120px;
			width: 120px;
			.row {
				margin: 4px;
				width: 110px;
				.inputContainer {
					height: 80px;
					.label {
						line-height: 80px;
						font-size: 14px;
					}
					&:after {
						height: 5px;
					}
					&.w-100 {
						width: 110px;
					}
					&.w-80 {
						width: 85px; // 220*.8
					}
					&.w-20 {
						width: 20px;
					}
					&.stopInput:after {
						height: 40px;
						font-size: 16px;
					}
				}
			}
			.outputContainer {
				margin: 0 4px;
				height: 20px;
				width: 110px;
				.label {
					line-height: 20px;
					font-size: 14px;
				}
			}
			.output {
				bottom: 0;
				right: 5px;
				font-size: 12px;
				line-height: 10px;
			}
		}
		.inputContainer, .outputContainer {
			&.stopReagent, &.stopInput {
				&:after {
					content: 'STOP';
					color: #fff;
					position: absolute;
					background-color: transparent;
					top: 0;
					left: 0;
					width: 100%;
					height: 50%;
					z-index: 10;
					pointer-events: none;
					@include noselect;
					@include flexbox;
					font-size: 24px;
					@include transform(rotate(-30deg));
					justify-content: center;
					align-items: center;
				}
			}
			&.slime .bar {
				background-color: $slime;
			}
			&.blood .bar {
				background-color: $blood;
			}
			&.water .bar {
				background-color: $water;
			}
			&.oxygen .bar {
				background-color: $oxygen;
			}
			&.ink .bar {
				background-color: $ink;
			}
			&.alkali .bar {
				background-color: $alkali;
			}
		}
	}
	.output {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		pointer-events: none;
		@include noselect;
		text-align: center;
		font-size: 14px;
		z-index: 10;
		@include transition(1s);
		// opacity: .2;
	}
}
