@import './modules';


.toastWrapper {
	position: absolute;
	top: 10px;
	left: 0;
	width: 100%;
	@include flexbox;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	pointer-events: none;
	@include md {
		top: 50px;
	}
	.toast {
		background-color: #444;
		position: relative;
		padding: 10px 12px;
		width: 100%;
		max-width: 400px;
		font-size: 22px;
		@include defBoxShadow;
		margin: 10px 0;
	}
}
