@import '../modules';

.brewing {
	margin: 20px;
	@include noselect;
	.brewFlasks {
		.item {
			margin: 10px 0;
			padding: 10px;
			border: 1px solid #000;
			@include transition(.3s);
			cursor: pointer;
			@include noselect;
			&:hover {
				background-color: #444;
			}
			.row {
				@include flexbox;
				justify-content: space-between;
				align-items: center;
				.costs {
				}
				.dur {
					color: #aaa;
				}
			}
			&.buyable {
				background-color: #363;
				&:hover {
					background-color: #484;
				}
			}
		}
	}
	.brewQueue {
		@include flexbox;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		.flask {
			margin: 6px 3px;
		}
		.queueProg {
			width: 100%;
			border: 1px solid #000;
			background-color: #222;
			position: relative;
			height: 40px;
			line-height: 40px;
			min-height: 42px;
			.item {
				position: absolute;
				top: 0; left: 0;
				text-align: center;
				height: 100%; width: 100%;
				z-index: 2;
				margin: 0;
				svg {
					margin-right: 5px;
				}
			}
			.bar {
				position: absolute;
				top: 0; left: 0;
				height: 100%;
				background-color: #112;
				z-index: 1;
			}
		}
	}
}
