@import '../modules';
@import '../mazesizes';


.combat {
	margin: auto 20px 10px 20px;
	.btnRow {
		@include flexbox;
		justify-content: space-between;
		align-items: center;

		.closeBtn {
			padding: 4px 8px;
			border: 1px solid #000;
			cursor: pointer;
			@include transition(.3s);
			&:hover {
				color: #f88;
				background-color: #444;
			}
		}
	}
	.title {
		font-size: 20px;
	}
	.details {
		@include flexbox;
		justify-content: flex-start;
		align-items: center;
		position: relative;
		.player, .enemy {
			margin: 10px 0;
			width: 50%;
			.progLabel {

			}
			.prog {
				height: 20px;
				position: relative;
				border: 1px solid #000;
				.bar {
					height: 100%;
					position: absolute;
					top: 0; left: 0;
					background-color: #363;
					z-index: 1;
					@include transition(.3s);
				}
				.label {
					z-index: 2;
					position: relative;
					top: 0; left: 0; height: 100%; width: 100;
					text-align: center;
					line-height: 20px;
				}
			}
		}
		.player {
			margin-right: 5px;
		}
		.enemy {
			margin-left: 5px;
		}
	}
	&.playerNeedHeal {
			.details:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			text-align: center;
			line-height: 80px;
			font-size: 42px;
			z-index: 2;
			background-color: rgba(#000, .4);
		}
		.details:before {
			content: 'Heal!';
			color: rgba(#fff, .3);
			position: absolute;
			@include transform(rotateZ(-15deg));
			top: 0;
			left: 0;
			width: 80%;
			height: 100%;
			text-align: center;
			line-height: 60px;
			font-size: 42px;
			z-index: 3;
		}
	}
	.map {
		@include flexbox;
		justify-content: center;
		align-items: center;
		width: 100%;
		flex-wrap: wrap;
		background-color: #000;
		padding: 2px;
		max-width: 400px;
		margin: 10px auto 0 auto;
		.cell {
			position: relative;
			height: 36px;
			line-height: 36px;
			width: calc(10% - 4px);
			text-align: center;
			border: 2px solid #2a2a2a;
			&.normal svg.fa-skull {
				font-size: 12px;
				opacity: .3;
			}
			&.boss svg {
				color: #822;
				font-size: 24px;
			}
			&.smallBoss svg {
				color: #622;
				font-size: 18px;
			}
		}
		&.isMap {
			background-color: #333;
			.cell {
				&.visited {
					background-color: #3a3a3a;
					background-color: #2a3a2a;
				}
				&.active {
					background-color: #555;
				}
				&.looted {
					svg {
						opacity: .3;
						color: #000;
					}
				}
			}
		}
		&.isMaze {
			.cell {
				background-color: #242;
				&.deadEnd {
					background-color: #322;
				}
				&.hasWallTop {
					border-top: 2px solid #000;
				}
				&.hasWallRight {
					border-right: 2px solid #000;
				}
				&.hasWallBottom {
					border-bottom: 2px solid #000;
				}
				&.hasWallLeft {
					border-left: 2px solid #000;
				}
				&.normal, .smallBoss, .boss {
					background-color: #322;
				}
				&.visited {
					background-color: #333;
				}
				&.active {
					background-color: #444;
				}
				&.prepared {
					background-color: #103;
				}
				&.looted {
					svg {
						opacity: .3;
						color: #000;
					}
				}
				&.hidden {
					border: 2px solid #1c1c1c;
					background-color: #222;
					svg {
						display: none;
					}
				}
			}
			@include mazeSizes();
		}
	}
	.usableFlasks {
		@include flexbox;
		justify-content: flex-start;
		align-items: center;
		margin-bottom: 10px;
		.flask {
			margin: 4px 2;
		}
	}
	.createDungeon {
		margin: 0 0 10px 0;
		.row {
			@include flexbox;
			justify-content: space-between;
			align-items: center;
			label {
				text-align: center;
			}
		}
	}
	.combatmenu {
		border-top: 1px solid #000;
		padding-top: 10px;
		margin: 10px 0 0 0;
		@include flexbox;
		justify-content: center;
		align-items: center;
		.col {
			width: 100%;
			&.col-3 {
				width: 25%;
			}
			.btn {
				margin: 4px 0;
				@include flexbox;
				justify-content: flex-start;
				align-items: center;

				.icon {
					svg {
						margin: 10px 20px;
					}
				}
				.text {
					div:first-child {
						font-size: 20px;
					}
					svg {
						margin-right: 15px;
					}
				}
			}
		}
	}
}
