@import '../modules';


.sidebarleft {
	background-color: #282828;
	@include flexbox;
	justify-content: flex-start;
	flex-direction: column;
	align-items: stretch;
	.storedFluid {
		margin: 20px 20px 0 20px;
		.fluid {
			height: 40px;
			line-height: 40px;
			position: relative;
			border: 1px solid #000;
			margin-bottom: 10px;
			@include noselect;
			.label {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				text-align: center;
				z-index: 2;
			}
			.bar {
				position: absolute;
				height: 100%;
				z-index: 1;
				@include transition(.3s);
			}
			&.slime {
				.bar {
					background-color: $slime;
				}
			}
			&.blood {
				.bar {
					background-color: $blood;
				}
			}
		}
	}
	.storedFlask {
		margin: 0 20px;
		@include flexbox;
		justify-content: center;
		align-items: center;
		.flask {
			margin: 0 5px;
		}
	}
	.upgrades {
		margin: 0 20px 20px 20px;
		@include noselect;
		.upgrade {
			margin: 10px 0;
			padding: 10px;
			border: 1px solid #000;
			@include transition(.3s);
			cursor: pointer;
			@include noselect;
			&:hover {
				background-color: #444;
			}
			.costs {
				text-align: right;
			}
			&.buyable {
				background-color: #363;
				&:hover {
					background-color: #484;
				}
			}
		}
	}

	.logs {
		margin: auto 10px 10px 10px;
		padding: 8px;
		// min-height: 300px;
		max-height: 250px;
		overflow-x: hidden;
		overflow-y: auto;
		background-color: #111;
		&::-webkit-scrollbar-track {
			-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
			background-color: #aaa;
			background-image: -webkit-linear-gradient(90deg,
			rgba(0, 0, 0, .8) 0%,
			rgba(0, 0, 0, .5) 50%,
			rgba(0, 0, 0, .8) 100%)
		}
		&::-webkit-scrollbar {
			width: 10px;
			background-color: #aaa;
			background-image: -webkit-linear-gradient(90deg,
			rgba(0, 0, 0, .8) 0%,
			rgba(0, 0, 0, .5) 50%,
			rgba(0, 0, 0, .8) 100%)
		}
		&::-webkit-scrollbar-thumb {
			background-color: #555;
			background-image: -webkit-linear-gradient(90deg,
			rgba(255, 255, 255, .1) 0%,
			transparent,
			rgba(255, 255, 255, .1) 100%)
		}
		.log {
			padding: 2px 8px;
			width: calc(100% - 18px);
			color: #aaa;
			background-color: #222;
			margin: 1px 0;
		}
	}
}

.sidebarleft .storedFluid .fluid.water .bar {
	background-color: $water;
}
.sidebarleft .storedFluid .fluid.oxygen .bar {
	background-color: $oxygen;
}
.sidebarleft .storedFluid .fluid.ink .bar {
	background-color: $ink;
}
.sidebarleft .storedFluid .fluid.alkali .bar {
	background-color: $alkali;
}
