/* breakpoints */
.sidebarleft {
  background-color: #282828;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: stretch; }
  .sidebarleft .storedFluid {
    margin: 20px 20px 0 20px; }
    .sidebarleft .storedFluid .fluid {
      height: 40px;
      line-height: 40px;
      position: relative;
      border: 1px solid #000;
      margin-bottom: 10px;
      -webkit-touch-callout: none;
      /* iOS Safari */
      -webkit-user-select: none;
      /* Safari */
      -khtml-user-select: none;
      /* Konqueror HTML */
      -moz-user-select: none;
      /* Firefox */
      -ms-user-select: none;
      /* Internet Explorer/Edge */
      user-select: none;
      /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }
      .sidebarleft .storedFluid .fluid .label {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        text-align: center;
        z-index: 2; }
      .sidebarleft .storedFluid .fluid .bar {
        position: absolute;
        height: 100%;
        z-index: 1;
        -moz-transition: 0.3s;
        -o-transition: 0.3s;
        -webkit-transition: 0.3s;
        transition: 0.3s; }
      .sidebarleft .storedFluid .fluid.slime .bar {
        background-color: #277554; }
      .sidebarleft .storedFluid .fluid.blood .bar {
        background-color: #AB3939; }
  .sidebarleft .storedFlask {
    margin: 0 20px;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center; }
    .sidebarleft .storedFlask .flask {
      margin: 0 5px; }
  .sidebarleft .upgrades {
    margin: 0 20px 20px 20px;
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }
    .sidebarleft .upgrades .upgrade {
      margin: 10px 0;
      padding: 10px;
      border: 1px solid #000;
      -moz-transition: 0.3s;
      -o-transition: 0.3s;
      -webkit-transition: 0.3s;
      transition: 0.3s;
      cursor: pointer;
      -webkit-touch-callout: none;
      /* iOS Safari */
      -webkit-user-select: none;
      /* Safari */
      -khtml-user-select: none;
      /* Konqueror HTML */
      -moz-user-select: none;
      /* Firefox */
      -ms-user-select: none;
      /* Internet Explorer/Edge */
      user-select: none;
      /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }
      .sidebarleft .upgrades .upgrade:hover {
        background-color: #444; }
      .sidebarleft .upgrades .upgrade .costs {
        text-align: right; }
      .sidebarleft .upgrades .upgrade.buyable {
        background-color: #363; }
        .sidebarleft .upgrades .upgrade.buyable:hover {
          background-color: #484; }
  .sidebarleft .logs {
    margin: auto 10px 10px 10px;
    padding: 8px;
    max-height: 250px;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #111; }
    .sidebarleft .logs::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      background-color: #aaa;
      background-image: -webkit-linear-gradient(90deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.5) 50%, rgba(0, 0, 0, 0.8) 100%); }
    .sidebarleft .logs::-webkit-scrollbar {
      width: 10px;
      background-color: #aaa;
      background-image: -webkit-linear-gradient(90deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.5) 50%, rgba(0, 0, 0, 0.8) 100%); }
    .sidebarleft .logs::-webkit-scrollbar-thumb {
      background-color: #555;
      background-image: -webkit-linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0%, transparent, rgba(255, 255, 255, 0.1) 100%); }
    .sidebarleft .logs .log {
      padding: 2px 8px;
      width: calc(100% - 18px);
      color: #aaa;
      background-color: #222;
      margin: 1px 0; }

.sidebarleft .storedFluid .fluid.water .bar {
  background-color: #2F4272; }

.sidebarleft .storedFluid .fluid.oxygen .bar {
  background-color: #718EA4; }

.sidebarleft .storedFluid .fluid.ink .bar {
  background-color: #11061D; }

.sidebarleft .storedFluid .fluid.alkali .bar {
  background-color: #AA7739; }
