@import '../variables.scss';
@import '../modules.scss';


.modal-background-disable {
  position: fixed;
  z-index: 90;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0.4;
}

.modal-content {
  z-index: 91;
  position: fixed;
  left: 50%;
  top: 30%;
	margin-left: -250px;
	margin-top: -100px;
	width: 500px;
	min-height: 200px;
	padding: 20px;
  background-color: #000;
	border-radius: 4px;
	@include flexbox;
	justify-content: center;
	flex-direction: column;
	@include defBoxShadow;
	@include animation('appear .8s ease');
	h2 {
		margin: 20px;
	}
	input {
		margin: 0 20px;
		border-radius: 4px;
		box-shadow: none;
		background-color: #eee;
		padding: 20px;
		border: 0;
		font-size: 18px;
		&:focus {
			outline: none;
		}
	}
	.closeBtn {
		position: absolute;
		top: 0;
		right: 0;
		color: #fff;
		font-size: 32px;
		padding: 4px 12px;
		@include transition(.3s);
		cursor: pointer;
		&:hover {
			background-color: #222;
		}
	}
	.btn {
		background-color: #222;
		border: 1px solid #444;
		margin: 10px 0;
	}
	textarea {
		width: 100%;
		min-height: 100px;
	}
}
@include keyframes(appear) {
	0% {
		top: 35%;
		opacity: 0;
	}

	100% {
		top: 30%;
		opacity: 1;
	}
}
