@import '../modules';


.clickComponent {
	margin: 20px;
	@include flexbox;
	justify-content: flex-start;
	align-items: flex-start;
	flex-wrap: wrap;
	.nextClickProg {
		height: 250px;
		width: 40px;
		border: 1px solid #000;
		position: relative;
		margin: 10px;
		.label {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			z-index: 2;
			line-height: 250px;
			text-align: center;
		}
		.bar {
			background-color: $c2;
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			z-index: 1;
		}
	}
	.clicker {
		height: 250px;
		margin: 10px;
		width: 250px;
		border: 1px solid #000;
		@include flexbox;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		position: relative;
		&:not(.isAuto) {
			.img {
				background-color: rgba($slime, .2);
				cursor: pointer;
				&:hover {
					background-color: rgba($slime, .5);
					.icon {
						color: lighten($slime, 10%);
					}
				}
			}
		}
		> .bar {
			position: absolute;
			bottom: 0;
			left: 0;
			height: 4px;
			background-color: rgba($slime, .5);
			z-index: -1;
		}
		.img {
			position: relative;
			height: 180px;
			width: 220px;
			background-color: #222;
			@include transition(.4s);
			.icon {
				width: 100%;
				text-align: center;
				line-height: 180px;
				font-size: 72px;
				color: $slime;
				@include transition(.4s);
			}
			.label {
				@include noselect;
				pointer-events: none;
				position: absolute;
				bottom: 6px;
				left: 0;
				width: 100%;
				text-align: center;
			}
		}
		.prog {
			@include noselect;
			position: relative;
			height: 40px;
			width: 220px;
			border: 1px solid #000;
			.label {
				position: absolute;
				top: 0;
				left: 0;
				line-height: 40px;
				width: 100%;
				text-align: center;
				z-index: 2;
			}
			.bar {
				position: absolute;
				top: 0;
				left: 0;
				background-color: $blood;
				height: 100%;
				z-index: 1;
				@include transition(.3s);
			}
		}
		&.shrink {
			height: 120px;
			width: 120px;
			margin: 5px;
			.img {
				height: 90px;
				width: 110px;
				.icon {
					font-size: 42px;
					line-height: 90px;
				}
			}
			.prog {
				height: 20px;
				width: 110px;
				.label {
					line-height: 20px;
				}
			}
		}
	}
}
.clickComponent .clicker.ghost:not(.isAuto) .img .icon {
	color: lighten($water, 25%);
	background-color: $water;
	&:hover {
		color: lighten($water, 35%);
		background-color: lighten($water, 5%);
	}
}
.clickComponent .clicker.ghost.isAuto .img .icon {
	color: lighten($water, 25%);
}
.clickComponent .clicker.ghost .prog .bar {
	background-color: $ink;
}
