/* breakpoints */
.brewing {
  margin: 20px;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }
  .brewing .brewFlasks .item {
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #000;
    -moz-transition: 0.3s;
    -o-transition: 0.3s;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }
    .brewing .brewFlasks .item:hover {
      background-color: #444; }
    .brewing .brewFlasks .item .row {
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .brewing .brewFlasks .item .row .dur {
        color: #aaa; }
    .brewing .brewFlasks .item.buyable {
      background-color: #363; }
      .brewing .brewFlasks .item.buyable:hover {
        background-color: #484; }
  .brewing .brewQueue {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; }
    .brewing .brewQueue .flask {
      margin: 6px 3px; }
    .brewing .brewQueue .queueProg {
      width: 100%;
      border: 1px solid #000;
      background-color: #222;
      position: relative;
      height: 40px;
      line-height: 40px;
      min-height: 42px; }
      .brewing .brewQueue .queueProg .item {
        position: absolute;
        top: 0;
        left: 0;
        text-align: center;
        height: 100%;
        width: 100%;
        z-index: 2;
        margin: 0; }
        .brewing .brewQueue .queueProg .item svg {
          margin-right: 5px; }
      .brewing .brewQueue .queueProg .bar {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        background-color: #112;
        z-index: 1; }
