@import '../modules';

.sidebar {
	background-color: #282828;
	@include flexbox;
	justify-content: flex-start;
	flex-direction: column;
	align-items: stretch;
	.upgrades {
		margin: 20px;
		@include noselect;
		.upgrade {
			margin: 10px 0;
			padding: 10px;
			border: 1px solid #000;
			@include transition(.3s);
			cursor: pointer;
			@include noselect;
			&:hover {
				background-color: #444;
			}
			.costs {
				text-align: right;
			}
			&.buyable {
				background-color: #363;
				&:hover {
					background-color: #484;
				}
			}
		}
	}
	.menu {
		height: 100vh;
		@include flexbox;
		justify-content: flex-start;
		align-items: stretch;
		flex-direction: column;
		.settings {
			margin: 20px;
			.title {
				font-size: 24px;
				margin: 10px 4px;
			}
			.subtitle {
				margin: 10px 4px;
				font-size: 20px;
			}
		}
		.menubar {
			@include flexbox;
			justify-content: flex-start;
			align-items: center;
			margin: 20px 20px 5px 20px;
			.item {
				margin: 4px;
				padding: 6px 12px;
				border: 1px solid #000;
				@include noselect;
				cursor: pointer;
				@include transition(.3s);
				&:hover {
					background-color: #444;
				}
				&.active {
					background-color: #222232;
					&:hover {
						background-color: #444;
					}
				}
			}
		}
		.selection {
			margin: 10px;
			.slider {
				display: none;
			}
			.title {
				font-size: 20px;
				text-align: left;
			}
			.btnRow {
				margin: 10px 0;
				@include flexbox;
				justify-content: flex-start;
				align-items: center;
				.title {
					text-align: center;
					width: 50%;
				}
			}
			.details {
				.title {
					margin: 4px 0;
				}
				.small {
					display: block;
					margin: 0;
					font-size: 12px;
				}
			}
			.closeBtn {
				background-color: #222;
				width: 40px;
				height: 40px;
				line-height: 40px;
				text-align: center;
				border: 1px solid #000;
				cursor: pointer;
				@include transition(.3s);
				&:hover {
					background-color: #444;
				}
			}
			.btn {
				margin: 10px 0;
				padding: 10px;
				border: 1px solid #000;
				cursor: pointer;
				@include transition(.3s);
				.name {
					font-size: 20px;
					font-weight: 700;
				}
				&:hover {
					background-color: #444;
				}
			}
			.reactorUpgrades {
				margin: 20px 0;
				@include noselect;
				.upgrade {
					margin: 10px 0;
					padding: 10px;
					border: 1px solid #000;
					@include transition(.3s);
					cursor: pointer;
					@include noselect;
					&:hover {
						background-color: #444;
					}
					.costs {
						text-align: right;
					}
					&.buyable {
						background-color: #363;
						&:hover {
							background-color: #484;
						}
					}
				}
			}
		}
	}
}
.formGroup {
	@include flexbox;
	justify-content: flex-start;
	align-items: center;
	margin: 10px 4px;
	&.checkbox {
		margin: 0 4px;
	}
	label, .label {
		min-width: 70px;
		margin-right: 5px;
	}
	input {
		padding: 4px 8px;
		font-size: 18px;
	}
	label.checkbox {
		margin: 0 10px;
		@include flexbox;
		justify-content: flex-start;
		align-items: center;
		input.checkbox {
			margin: 8px 10px;
		}
	}
}
